.card {
    background: #fff;
    border-radius: 2px;
    height: 250px;
    margin: 10px;
    width: 260px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    overflow: hidden;
  }
  
  /* Slide */
   .hover img {
      margin-left: 10px;
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
  }
  
  
.wrapper {
    padding-top: 10px;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
  
  }
.gameScore {
    text-align: center;
    height: 70px;
    position: relative;
    line-height: 1em;
    padding: 0;
    margin: 0;
  }
  
  h3.score {
    margin-top: 5px;
    padding: 0;
    font-weight: 600;
  }
  h3.status{
    margin: 0;
    padding: 0;
    color: red;
  }
body {
    background: lightblue;
}

.App-header {
    background-color: darkblue;
    height: 100px;
    padding: 10px;
    color: white;
  }
  
  .App-title {
    font-size: 2em;
  }
  
  .App-intro {
    font-size: large;
  }
* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
  }
  
  h1 {
    padding: 0;
    margin: 3px;
  }
